import { filter, includes } from 'lodash-es';
import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { GENERAL_CATEGORIES } from '../../src/webview/blockly/toolbox/categories/block_data';

function MockPluginPanel():JSX.Element {
  const [types, set_types] = useState<string[]>([]);
  const [img_src, set_img_src] = useState<string>('#');

  useEffect(() => {
    let new_types = [];

    Object.keys(GENERAL_CATEGORIES).forEach((c) => {
      const blocks = GENERAL_CATEGORIES[c];
      const new_list = filter(blocks, (b) => !includes(b, 'flyout_gap'));
      new_types = new_types.concat(new_list);
    });

    set_types(new_types);
  }, []);

  return (
    <div
      style={{
        maxHeight: '100vh',
        width: '50vw',
        display: 'flex',
      }}
    >
      <div style={{
        width: '50%',
      }}>
        <img
          src={img_src}
          style={{
            maxWidth: '100%',
          }}
        />
      </div>
      <div
        style={{
          width: '50%',
          overflow: 'scroll',
        }}
      >
        <h1>积木列表</h1>
        {types.map((type) => (
          <button
            style={{
              display: 'block',
              minWidth: '100px',
              maxWidth: '100%',
              height: '40px',
              marginBottom: '10px',
              fontSize: '18px',
            }}
            key={type}
            onClick={async() => {
              try {
                const result = await (window as any).nemo_plugin_bridge.block_type_to_image({
                  block_type: type,
                });
                set_img_src(result.data_url);
              } catch (err) {
                console.error(err);
              }
            }}
          >
            {type}
          </button>
        ))}
      </div>
    </div>
  );
}

const root = document.querySelector<HTMLDivElement>('#mock_plugin_root');

if (root) {
  ReactDOM.render(<MockPluginPanel />, root);
}
